import React, { Fragment, Component } from 'react';
 import Intercom from 'react-intercom';
import Slider from 'react-slick';

import img from '../../../assets/utils/images/app_logo.png';

import { Col, Row } from 'reactstrap';
import MerchantOnbording from './Merchants/Variation1';
import createMerchant from '../../../actions/merchants/createMerchant';
import { connect } from 'react-redux';
import { toast } from 'react-toastify';
import PageTitle from '../../../Layout/AppMain/PageTitle';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
export class Register extends Component {
         state = {
           merchant: {
             name: null,
             email: null,
             phone: null,
             details: null,
             tagline: null,
           },
           merchant_image: [],
           success: null,
           errors: {},
           terms: false,
         };

         onDrop = (files) => {
           this.setState({
             merchant_image: files.map((file) =>
               Object.assign(file, {
                 preview: URL.createObjectURL(file),
               })
             ),
           });
         };

         //Handle cancel upload
         onCancel = () => {
           this.setState({
             merchant_image: [],
           });
         };

         cancle = () => this.props.history.push('/dashboard/products');

         //Handle validation

         handleValidation() {
           let fields = this.state.merchant;
           let errors = {};
           let formIsValid = true;

           //Name
           if (!fields['name']) {
             formIsValid = false;
             errors['name'] = 'Business name is required';
           }

           if (fields['tagline'] === null) {
             formIsValid = false;
             errors['tagline'] = ' Tag line is required';
           }

           if (fields['details'] === null) {
             formIsValid = false;
             errors['details'] = 'Please enter a description';
           }

           if (fields['phone'] === null) {
             formIsValid = false;
             errors['phone'] = 'Please enter a valid phone number';
           }

           //  if (fields['name'] !== null ) {
           //    if (!fields['name'].match(/^[a-zA-Z]+$/)) {
           //      formIsValid = false;
           //      errors['name'] = 'Only letters';
           //    }
           //  }

           if (this.state.merchant_image.length === 0) {
             formIsValid = false;
             errors['image'] = 'Image is required';
           }

           //Email
           if (!fields['email']) {
             formIsValid = false;
             errors['email'] = 'Email Cannot be empty';
           }

           if (fields['email'] !== null) {
             let lastAtPos = fields['email'].lastIndexOf('@');
             let lastDotPos = fields['email'].lastIndexOf('.');

             if (
               !(
                 lastAtPos < lastDotPos &&
                 lastAtPos > 0 &&
                 fields['email'].indexOf('@@') == -1 &&
                 lastDotPos > 2 &&
                 fields['email'].length - lastDotPos > 2
               )
             ) {
               formIsValid = false;
               errors['email'] = 'Email is not valid';
             }
           }

           this.setState({ errors: errors });
           return formIsValid;
         }

         handleCheck= () => {
           console.log('yess')
           this.setState({ terms: !this.state.terms });
         };

         //Handles input change
         handleInputChange = (e) => {
           e.preventDefault();
           // const data = {
           //   type: e.target.name,
           //   content: e.target.value,
           // };
           //const errors = Validations(data, this.state.error);
           const { merchant } = this.state;
           console.log(this.state);
           this.setState({
             merchant: {
               ...merchant,
               [e.target.name]: e.target.value,
             },
           });
         };

         handleSubmit = async () => {
           const { createMerchant } = this.props;
           const { merchant } = this.state;

           // if (Object.keys(error).length > 0) {
           //   toast.error('Invalid Fields', {
           //     position: toast.POSITION.TOP_RIGHT,
           //   });
           //   return false;
           // }
           if (!this.handleValidation()) {
             toast.error('Invalid fields kindly fix the errors');
             return false;
           }

           if (!this.state.terms) {
             toast.error('Kindly accept the terms and conditions to proceed');
             return false;
           }

           if (this.state.merchant_image.length === 0) {
             toast.error('Business logo is required');
             return false;
           }
           let data = new FormData();
           for (let i in merchant) {
             data.append(i, merchant[i]);
           }

           const { merchant_image } = this.state;
           for (let img = 0; img < merchant_image.length; img++) {
             data.append('merchant_image', merchant_image[0]);
           }

           // data.append('product_image', JSON.stringify(product_image));
           await createMerchant(data);
           console.log(this.props.Merchant.success);
           if (this.props.Merchant.success) {
             console.log(this.props.Merchant.success);
             this.setState({
               success: true,
             });
           }
         };
         render() {
           return (
             <Fragment>
               <CSSTransitionGroup
                 component='div'
                 transitionName='TabsAnimation'
                 transitionAppear={true}
                 transitionAppearTimeout={0}
                 transitionEnter={false}
                 transitionLeave={false}
               >
                 <Intercom appID='wz6k5k91' />
                 <Row
                   style={{
                     width: '80%',
                     margin: '0 auto',
                   }}
                   className=' no-gutters'
                 >
                   <Col lg='12' md='12'>
                     <PageTitle
                       heading='Merchant Self Onboarding'
                       subheading='Create Account'
                       img={img}
                     />
                   </Col>
                 </Row>

                 <Row
                   style={{
                     width: '80%',
                     margin: '0 auto',
                   }}
                 >
                   <Col
                     lg='12'
                     md='12'
                     className=' mt-4 d-md-flex d-sm-block h-100 '
                   >
                     <MerchantOnbording
                       errors={this.state.errors}
                       handleInputChange={this.handleInputChange}
                       handleSubmit={this.handleSubmit}
                       onDrop={this.onDrop}
                       onCancel={this.onCancel}
                       success={this.state.success}
                       files={this.state.merchant_image}
                       handleCheck={this.handleCheck}
                     />
                   </Col>
                 </Row>
               </CSSTransitionGroup>
             </Fragment>
           );
         }
       }

const mapDispatchToProps = (dispatch) => ({
  createMerchant: (merchant) => dispatch(createMerchant(merchant)),
});
const mapStateToProps = (state) => ({
  ...state,
});

export default connect(mapStateToProps, mapDispatchToProps)(Register);
