import axios from 'axios';
import { toast } from 'react-toastify';

import * as types from '../action_types';

const createMerchantSuccess = (payload) => ({
  type: types.CREATE_MERCHANT_SUCCESS,
  payload,
});

const createMerchantFailed = (payload) => ({
  type: types.CREATE_MERCHANT_FAILED,
  payload,
});

const startCreateMerchant = () => ({
  type: types.API_REQUEST,
});

const createMerchant = (merchant) => (dispatch) => {
  dispatch(startCreateMerchant());
  return axios
    .post('https://gateway.julla.co/auth/api/auth/v2/merchant', merchant)
    .then((res) => {
      toast.success('Bussiness details added successfully');
      localStorage.setItem('user', JSON.stringify(res.data.merchant))
      localStorage.setItem('token', res.data.token)
      console.log(res.data);
      dispatch(createMerchantSuccess(res.data));
    })
    .catch((err) => {
      console.log(err.response);
      toast.error(err.response.data.message);
      dispatch(createMerchantFailed(err));
    });
};

export default createMerchant;
